import React from 'react';
import Flags from './flags';
import './flaglist.css';

const flaglist = ({ flagsarray }) => {
    return (
        <div>{

            flagsarray.map((user, i) => {
                return (
                    <div className="container">
                        <Flags name={flagsarray[i].name} img={flagsarray[i].img} />
                    </div>
                )
            })
        }
        </div>
    )
}

export default flaglist;
